import { InnerBlocks } from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"

registerBlockType("voblocktheme/banner", {
  title: "Banner",
  supports: {
    align: ["full"]
  },
  attributes: {
    align: { type: "string", default: "full" },
    title: { type: "string" },
    body: { type: "string" }
  },
  edit: EditComponent,
  save: SaveComponent
})

function EditComponent(props) {
  function updateTitle(e) {
    props.setAttributes({ title: e.target.value })
  }

  function updateBody(e) {
    props.setAttributes({ body: e.target.value })
  }

  return (
    <header id="masthead" className="banner">
      <div className="banner-item">
        <div className="mobile-banner-img hidden-sm-up">
          <img src="" id="randomImage" alt="#" />
        </div>
        <InnerBlocks allowedBlocks={["voblocktheme/slideshow"]} />
        <div className="banner-overlay valign-end disblock-xs-down">
          <div className="banner-overlay-cont valign-middle-item">
            <div className="banner-txt text-shadow-black" style={{paddingTop: "500px"}}>
              <input type="text" placeholder="Title" className="h1 flush-top" value={props.attributes.title} onChange={updateTitle} />
              <textarea placeholder="Body Text" className="large hidden-xs-down flush-bottom" value={props.attributes.body} onChange={updateBody} />
            </div>
            <img src="https://res.cloudinary.com/ventureoutdoors/image/upload/q_auto,f_auto/vector-images/feather-divider.svg" className="imgdivider small hidden-xs-down" />
          </div>
        </div>
      </div>
      <div class="banner-form">
        <div class="banner-form__content">
          <h2 class="h4 flush-top">Find Your Ideal Outdoor Activity</h2>
          <div class="row">
            <div class="col-sm-3">
              <div class="form-select">
                <div class="facetwp-facet facetwp-facet-frontpage_activity_type facetwp-type-dropdown" data-name="frontpage_activity_type" data-type="dropdown">
                  <select id="activity_type" class="facetwp_dropdown">
                    <option value="">Activity Type</option>
                  </select>
                </div>
              </div>
            </div>
            <div class="col-sm-3">
              <div class="form-select">
                <div class="facetwp-facet facetwp-facet-frontpage_activity_length facetwp-type-dropdown" data-name="frontpage_activity_length" data-type="dropdown">
                  <select id="activity_length" class="facetwp_dropdown">
                    <option value="">Activity Length</option>
                  </select>
                </div>
              </div>
            </div>
            <div class="col-sm-3">
              <div class="form-select">
                <div class="facetwp-facet facetwp-facet-frontpage_difficulty_level facetwp-type-dropdown" data-name="frontpage_difficulty" data-type="dropdown">
                  <select id="difficulty" class="facetwp_dropdown">
                    <option value="">Difficulty</option>
                  </select>
                </div>
              </div>
            </div>
            <div class="col-sm-3">
              <button class="btn-primary btn-block js-frontpage-filter">View Activities</button>
            </div>
          </div>
          <p class="flush-bottom"><span class="js-number-results">0</span> activities meet your criteria</p>
        </div>
      </div>
    </header>
  )
}

function SaveComponent(props) {
  return <InnerBlocks.Content />
}